import React from 'react';
import { IonContent, IonPage, IonTitle, IonItem, IonTabs, IonRouterOutlet, IonTabBar, IonTabButton, IonIcon, IonLabel, IonCard, IonCardContent } from '@ionic/react';
import './Tab3.css';
import { ILanding, IAuthReduxProps } from '../types/interfaces';
import LoginModal from '../components/auth/LoginModal';
import RegisterModal from '../components/auth/RegisterModal';
import { connect } from 'react-redux';
import Tab1 from './Tab1';
import { Route, Redirect } from 'react-router-dom';
import { IonReactRouter } from '@ionic/react-router';
import Tab2 from './Tab2';
import Tab3 from './Tab3';
import EventEdit from '../components/EventEdit';
import EventLog from '../components/EventLog';
import { calendar, checkmarkCircle, cog } from 'ionicons/icons';


const Landing = ({ auth }: ILanding) => {

  const authLinks = (
    <IonReactRouter>
      <IonTabs>
        <IonRouterOutlet>
            <Route path="/" render={() => <Redirect to="/tab1" />} exact={true} />
            <Route path="/tab1" component={Tab1} />
            <Route path="/tab2" component={Tab2} />
            <Route path="/tab3" component={Tab3} />
            <Route path="/event/:_id" component={EventEdit} exact={true} />
            <Route path="/event/log/:_id" component={EventLog} exact={true}/>
        </IonRouterOutlet>

        <IonTabBar slot="bottom">
          <IonTabButton tab="tab1" href="/tab1">
          <IonIcon icon={calendar}></IonIcon>
            <IonLabel>Calendar</IonLabel>
          </IonTabButton>
          <IonTabButton tab="tab2" href="/tab2">
            <IonIcon icon={checkmarkCircle} />
            <IonLabel>Log</IonLabel>
          </IonTabButton>
          <IonTabButton tab="tab3" href="/tab3">
            <IonIcon icon={cog} />
            <IonLabel>Settings</IonLabel>
          </IonTabButton>
        </IonTabBar>
      </IonTabs>
    </IonReactRouter>
    
  );

  const guestLinks = (
    <IonPage>
    <IonContent>
      <IonCard>
          <IonItem>
            <IonTitle>
            <div className="ion-text-center">
              Welcome to Tardy
              </div>
              </IonTitle>
          </IonItem>
          <IonCardContent>
          <LoginModal />
          <RegisterModal />
      </IonCardContent>
        </IonCard>
    </IonContent>
    </IonPage>
  );
  
  return (
    <div>
      {auth && auth.isAuthenticated ? authLinks : guestLinks}
    </div>
  );
};

const mapStateToProps = (state: IAuthReduxProps) => ({
  auth: state.auth
});

export default connect(mapStateToProps, null)(Landing);



