import React, {useState, useEffect} from 'react';
import Clock from 'react-live-clock';
import { IonContent, IonItem, IonButton, IonLabel, IonPage, IonHeader, IonToolbar, IonTitle, IonDatetime, IonButtons, IonBackButton } from '@ionic/react';

import { logArrival } from "../flux/actions/eventActions";
import { IEventLogReduxProps, IEventLog } from '../types/interfaces';
import {connect} from 'react-redux';


const EventLog = ({current, logArrival}: IEventLog) => {
 
  const [title, setTitle] = useState('');
  const [location, setLocation] = useState('');
  const [dateStart, setDateStart] = useState('');
  const [dateEnd, setDateEnd] = useState('');
  const [attendee, setAttendee] = useState('');
  const [arrivalTime, setArrivalTime] = useState('');


  useEffect(() => {
    if(current) {
      setTitle(current.title)
      setLocation(current.location)
      setDateStart(current.dateStart)
      setDateEnd(current.dateEnd)
      setAttendee(current.attendee)
      setArrivalTime(current.arrivalTime)
    }
  }, [current]);  

 const handleClick = () => {

   setArrivalTime(Date());
 }

 useEffect(() =>{
 // 
  const updatedLog = {
    _id: current._id,
    title: current.title,
    location: current.title,
    dateStart: current.title,
    dateEnd: current.title,
    attendee: current.title,
    arrivalTime
  };
      // Log event via logArrival action
      logArrival(updatedLog);
      // Clear Fields
      setTitle('');
      setLocation('');
      setDateStart('');
      setDateEnd('');
      setAttendee('');
      // setArrivalTime('');
 }, [arrivalTime]);



//make upcoming event display on the page, but make it disabled until 15mins before the event time. 

// const logArrival;
//Example 15min max allowable time to arrive

const meetStart = new Date(current.dateStart);
const arrivedString = new Date(Date.parse(arrivalTime)) 

let arrivalmessage
if(meetStart > arrivedString ){
  arrivalmessage = "You have arrived on time!"
} else if (meetStart < arrivedString ){
  arrivalmessage = "You're late!"
} else {
  arrivalmessage = "You have not arrived yet."
}

  return(
      <IonPage>
        <IonHeader>
          <IonToolbar>
            <IonButtons slot="start">
              <IonBackButton defaultHref="/"/>
            </IonButtons>
            <IonTitle><div className="ion-text-center">
            Log
            </div></IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
        {/* <form onSubmit={handleOnSubmit}> */}
        <IonItem>
        {current.title}
        <br/>
        {current.location}      
        </IonItem>
        <IonItem>
          <IonLabel>
            Scheduled Time: 
          </IonLabel>
          <IonDatetime value={current.dateStart} readonly displayFormat="MMM DD, h:mm A" ></IonDatetime>
          <IonDatetime value={current.dateEnd} readonly displayFormat="MMM DD, h:mm A" ></IonDatetime>
        </IonItem>
        <IonItem color="success">
          <IonLabel>
            Time now
          </IonLabel>
          <Clock 
            format={'HH:mm:ss A'} 
            ticking={true}
            timezone={''}
          />
        </IonItem>
        <IonItem>
          <IonLabel>
            You
          </IonLabel>
          <IonDatetime 
          placeholder={arrivalTime}
          value={arrivalTime} 
          readonly 
          displayFormat="MMM DD, h:mm A" 
          // onIonChange={e => setArrivalTime(e.detail.value!)}
          ></IonDatetime>
        </IonItem>
        <IonItem>
          <IonLabel>
          <div className="ion-text-center">
          {arrivalmessage}
          </div>
          </IonLabel>
        {/* <CircularProgressbar value={value} maxValue={20} text={`${value}mins`} /> */}
        </IonItem>
        {/* </form>  */}
          <IonButton 
          type="submit"
          expand="block"
          onClick={handleClick}
          disabled={arrivalTime? true : false}
          >Log</IonButton>
           {/* <IonProgressBar
           color="primary" value={0.25}
           ></IonProgressBar> */}
        {/* </form> */}
      </IonContent>
      </IonPage>
  );
};

const mapStateToProps = (state: IEventLogReduxProps) => ({
  current: state.event.current
});


export default connect(mapStateToProps, {logArrival})(EventLog);
