import React, { useState, useCallback } from 'react';
import { IonContent, IonItem, IonButton, IonButtons, IonToolbar, IonTitle, IonHeader, IonModal, IonSearchbar } from "@ionic/react";


const LoginModal = () => {

  const [modal, setModal] = useState(false);
  const [searchText, setSearchText] = useState('');
  // const [data, setData] = useState();


  const handleToggle = useCallback(() => {
    // Clear errors
    setModal(!modal);
  }, [modal]);


  // const handleOnSubmit = (e: any) => {
  //   e.preventDefault();
  // };

  return (
    <div>
      <IonItem onClick={handleToggle}>
        Invitee
      </IonItem>
      <IonModal isOpen={modal} onDidDismiss={() => setModal(false)}>        
        <IonContent>
            {/* <IonToast 
              position="top"
              isOpen={showToast1} 
              onDidDismiss={() => setShowToast1(false)} 
              message={msg}
              duration={2000}
              />  */}
          <form>
          <IonHeader>
              <IonToolbar>
              <IonButtons slot="start">
                <IonButton onClick={() => setModal(false)}>
                    Cancel
                </IonButton>
              </IonButtons>
              <IonTitle>
                <div className="ion-text-center">
                Invitee
                </div>
              </IonTitle>
            </IonToolbar>
            </IonHeader>
              <IonItem>
              <IonSearchbar 
              value={searchText} 
              onIonChange={e => setSearchText(e.detail.value!)}
              />
              </IonItem>
            
              {/* <IonButton
              onClick={handleOnSubmit}
              >
                Login
              </IonButton> */}
          </form>
        </IonContent>
      </IonModal>
    </div>
  );
};


export default (LoginModal);