import React, { useState } from "react";
import { IonContent, IonItem, IonInput, IonButton, IonItemDivider, IonLabel, IonDatetime, IonButtons, IonToolbar, IonTitle, IonHeader } from "@ionic/react";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

import {addEvent} from '../flux/actions/eventActions';
import {connect} from 'react-redux';
import {IEventReduxProps, IEventAdd} from '../types/interfaces';
import {addMinutes, roundToNearestMinutes} from "date-fns";
import AttendeeModal from "./AttendeeModal";


/* Theme variables */
// import "./theme/variables.css";



const EventAdd = ({addEvent, onDismissModal}: IEventAdd) => {

  //For dateStart Placeholder
  var b = roundToNearestMinutes(new Date(), { nearestTo: 5 });
  var c = b.toString();

  //For dateEnd Placeholder
  var d = addMinutes(new Date(), 30);
  var p = roundToNearestMinutes(d, { nearestTo: 5 });
  var n = p.toString();

// function checkEndisAfterStart
//   c >= n

    const [title, setTitle] = useState('');
    const [location, setLocation] = useState('');
    const [dateStart, setDateStart] = useState(c);
    const [dateEnd, setDateEnd] = useState(n);
    const [attendee, setAttendee] = useState('');


    // const handleChangeTitle = (e: ITarget) => setTitle(e.target.value);
    const handleOnSubmit = (e: any) => {
      e.preventDefault();
  
      const newEvent = {
        title,
        location,
        dateStart,
        dateEnd,
        attendee,
      };
      // Add item via addItem action
      addEvent(newEvent);
    //   setTitle({title:''});
 };

  return (
      <IonContent fullscreen={true}>
          <form onSubmit={handleOnSubmit}>
              <IonHeader>
              <IonToolbar>
              <IonButtons slot="start">
                <IonButton onClick={onDismissModal}>
                    Cancel
                </IonButton>
              </IonButtons>
              <IonTitle>
                <div className="ion-text-center">
                New Event
                </div>
              </IonTitle>
              <IonButtons slot="end">
                <IonButton type="submit" onClick={onDismissModal}>
                    Add
                </IonButton>
              </IonButtons>
            </IonToolbar>
            </IonHeader>
            <IonItem>
                <IonInput
                placeholder="Title"
                type="text"
                onIonChange={e => setTitle(e.detail.value!)}
                name="title"
              />
            </IonItem>

            <IonItem>
                <IonInput
                placeholder="Location"
                type="text"
                onIonChange={e => setLocation(e.detail.value!)}
                name="location"
              />
            </IonItem>
            <IonItemDivider color="light"></IonItemDivider>
            <IonItem>
                <IonLabel>Starts</IonLabel>
                <IonDatetime
                value={dateStart}
                name="dateStart"
                onIonChange={e => setDateStart(e.detail.value!)}
                displayFormat="MMM DD, h:mm A" 
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                // rules={{
                //   required: true,
                //   minLength: { value: 4, message: "Must be 4 chars long" }
                // }}
              />
            </IonItem>
            <IonItem>
                <IonLabel>Ends</IonLabel>
                <IonDatetime
                value={dateEnd}
                name="dateEnd"
                onIonChange={e => setDateEnd(e.detail.value!)}
                displayFormat="MMM DD, h:mm A" 
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                // rules={{
                //   required: true,
                //   minLength: { value: 4, message: "Must be 4 chars long" }
                // }}
              />
            </IonItem>     
            <IonItemDivider color="light"></IonItemDivider>
            <IonItem>
                <IonInput
                placeholder="Invitee"
                type="text"
                onIonChange={e => setAttendee(e.detail.value!)}
                name="attendee"
              />
            </IonItem>
            <IonItem>
            <AttendeeModal />
            </IonItem>
          </form>
        </IonContent>
  );
};

const mapStateToProps = (state: IEventReduxProps) => ({
    event: state.event,
  });

export default connect(mapStateToProps, {addEvent})(EventAdd);