import React, { useState, useEffect } from 'react';
import { IonItemDivider, IonPage, IonTitle, IonItem, IonLabel, IonDatetime, IonButton, IonInput, IonHeader, IonToolbar, IonButtons, IonBackButton, IonContent } from '@ionic/react';
import { updateEvent } from "../flux/actions/eventActions";
import { IEventEditReduxProps, IEventEdit } from '../types/interfaces';
import {connect} from 'react-redux';

//make upcoming event display on the page, but make it disabled until 15mins before the event time.

const EventEdit = ({ current, updateEvent }:IEventEdit) => {

    const [title, setTitle] = useState('');
    const [location, setLocation] = useState('');
    const [dateStart, setDateStart] = useState('');
    const [dateEnd, setDateEnd] = useState('');
    const [attendee, setAttendee] = useState('');

    useEffect(() => {
      if(current) {
        setTitle(current.title)
        setLocation(current.location)
        setDateStart(current.dateStart)
        setDateEnd(current.dateEnd)
        setAttendee(current.attendee)
      }
    }, [current]);

    const handleOnSubmit = (e: any) => {
      e.preventDefault();
  
      const updatedEvent = {
        _id: current._id,
        title,
        location,
        dateStart,
        dateEnd,
        attendee
      };
      // Update event via updateEvent action
      updateEvent(updatedEvent);
      // Clear Fields
      setTitle('');
      setLocation('');
      setDateStart('');
      setDateEnd('');
      setAttendee('');
    };
    
  return(
    <IonPage>
      <IonContent>
      <form onSubmit={handleOnSubmit}>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonBackButton defaultHref="/" text="Cancel" icon=""/>
          </IonButtons>
          <IonTitle>
          <div className="ion-text-center">
          Update Event
          </div>
          </IonTitle>
          <IonButtons slot="end">
            <IonButton type="submit" routerLink={"/tab1"} >
              Done
            </IonButton>
          </IonButtons>
        </IonToolbar>
      </IonHeader>
            <IonItem>
                <IonInput
                // disabled
                // readonly
                placeholder="Title"
                type="text"
                value={title}
                onIonChange={e => setTitle(e.detail.value!)}
                name="title"
              />
            </IonItem>

            <IonItem>
                <IonInput
                placeholder="Location"
                type="text"
                onIonChange={e => setLocation(e.detail.value!)}
                name="location"
                value={location}
              />
            </IonItem>
            <IonItemDivider color="light"></IonItemDivider>
            <IonItem>
                <IonLabel>Starts</IonLabel>
                <IonDatetime
                value={dateStart}
                name="dateStart"
                onIonChange={e => setDateStart(e.detail.value!)}
                displayFormat="MMM DD, h:mm A" 
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                // rules={{
                //   required: true,
                //   minLength: { value: 4, message: "Must be 4 chars long" }
                // }}
              />
            </IonItem>
            <IonItem>
                <IonLabel>Ends</IonLabel>
                <IonDatetime
                value={dateEnd}
                name="dateEnd"
                onIonChange={e => setDateEnd(e.detail.value!)}
                displayFormat="MMM DD, h:mm A" 
                minuteValues="0,5,10,15,20,25,30,35,40,45,50,55"
                // rules={{
                //   required: true,
                //   minLength: { value: 4, message: "Must be 4 chars long" }
                // }}
              />
            </IonItem>     
            <IonItemDivider color="light"></IonItemDivider>
            <IonItem>
                <IonInput
                placeholder="Invitees"
                type="text"
                value={attendee}
                onIonChange={e => setAttendee(e.detail.value!)}
                name="attendee"
              />
            </IonItem>
          </form>
          </IonContent>
    </IonPage>
  );
};


const mapStateToProps = (state: IEventEditReduxProps) => ({
  current: state.event.current,
});


export default connect(mapStateToProps, {updateEvent})(EventEdit);
