import React, { useEffect } from 'react';
import { IonContent, IonItem, IonList, IonLabel, IonCard, IonCardHeader, IonCardTitle, IonCardContent } from '@ionic/react';
import { ICalendar, IEventReduxProps } from '../types/interfaces';
import { connect } from 'react-redux';
import {getEvents} from '../flux/actions/eventActions';

const Leaderboard = ({ getEvents, event }:ICalendar) => {
 
// Time saved
// Count of events on time
// Count of events late
useEffect(() => { 
    getEvents(); 
  }, [getEvents]);
 
  const { events } = event;
  
  //Number of meetings logged
  const logged = events
    .filter(event => event.arrivalTime != null)
  const numLogged = logged.length
    
  //# of times arrived on time
  const onTime = events
  .filter(event => event.arrivalTime < event.dateStart)
  const numOnTime = onTime.length
  //% on time
  const perOnTime = Math.round((numOnTime/numLogged)*100)

  return(
      <IonContent>
        <IonCard>
            <IonCardHeader>
                <IonCardTitle>My Stats</IonCardTitle>
            </IonCardHeader>
            <IonCardContent>
                <IonLabel>
                    On Time: {perOnTime}%
                </IonLabel>
                <br />
                <IonLabel>
                    Total Meetings Attended: {numLogged}
                </IonLabel>
                <br />
                <IonLabel>
                    Rewards Earned: 
                </IonLabel>
            </IonCardContent>
        </IonCard>
        <IonList>
          {events.map(({_id, title, arrivalTime}) =>  
          <IonItem key={_id}>
              {title}
              {arrivalTime}
          </IonItem>
          )}
      </IonList>
      </IonContent>
  );
};

const mapStateToProps = (state: IEventReduxProps) => ({
  event: state.event
});

export default connect(mapStateToProps, { getEvents })(Leaderboard);

